<template>
  <div class="xfgj" v-loading="!data.length"
       :element-loading-background="$STANDARD.loadingBgColor">
    <div class="container">
      <ul class="list clearfloat">
        <li class="item-li" v-for="(item,index) of data" :key="index">
          <div class="inner" :class="popShowIndex===index?'active':''">
            <p class="time">重复次数：{{item.time}}</p>
            <i class="icon"></i>
            <p class="date">{{item.date}}</p>
            <i class="circle" @click="popShowIndex=index"></i>
          </div>
          <transition name="fade">
            <div class="arrow" v-if="popShowIndex===index"></div>
          </transition>
          <transition name="fade">
            <ul class="pop clearfloat" v-if="popShowIndex===index">
              <li class="pop-li" v-for="(it,i) of item.data" :key="i">
                <p class="name">{{it.name}} : </p>
                <p class="value" :title="it.value">{{it.value}}</p>
              </li>
            </ul>
          </transition>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  export default {
    name: "xfgj",
    props: {
      data: Array
    },
    data() {
      return {
        popShowIndex: 3
      }
    },
    computed: {
      styleType() {
        return this.$store.state.styleType;
      }
    },
  }
</script>

