<template>
  <div class="wrapper">
    <div class="content">
      <div class="w-left">
        <left @change="leftNameClick"></left>
      </div>
      <div class="w-right">
        <div class="w-r-l">
          <ctitle :title="'基本信息'"></ctitle>
          <div class="inner">
            <jbxx :data="jbxxData"></jbxx>
          </div>
        </div>
        <div class="w-r-r">
          <div class="top">
            <div class="left">
              <ctitle :title="'信访烈度'"></ctitle>
              <div class="inner">
                <radar :params="xfldRadarParams"></radar>
              </div>
            </div>
            <div class="right">
              <ctitle :title="'投诉问题占比'"></ctitle>
              <div class="inner">
                <loopPie :params="tswtPieParams"></loopPie>
              </div>
            </div>
          </div>
          <div class="btm">
            <ctitle :title="'信访轨迹分析'"></ctitle>
            <div class="inner">
              <xfgj :data="xfgjData"></xfgj>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {get_http} from "@/api/index";
  import {gradientColor} from "@/utils/gradient";
  import ctitle from "@/components/title/index";
  import radar from "@/components/radar/radar";
  import loopPie from "@/components/pie/LoopPie";
  import xfgj from "../component/xfgj";
  import jbxx from "../component/jbxx";
  import left from "../component/left";
  export default {
    name: "lmwsts",
    components: {
      ctitle,
      radar,
      loopPie,
      xfgj,
      jbxx,
      left
    },
    data(){
      return{
        // 2.基本信息
        jbxxData:null,
        //3信访烈度
        xfldRadarParams: {
          data: [],
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          options: {
            center: ['50%', '50%'],
            radius:'70%'
          }
        },
        //4投诉问题占比
        tswtPieParams: {
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            bgSize: 220,
            colors: this.$STANDARD.pieColors,
            radius: ["45%", "55%"],
            center: ["50%", "50%"],
            centerRadius: ["40%", "41%"],
            labelRich: {
              b: this.fontSize(0.18),
              d: this.fontSize(0.2),
              bHeight: this.fontSize(0.3)
            },
            length: 20,
            length2: 70,
            // borderColor: "#050f28",
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
            },
            rich: {
              bfontsize: "16%" //文字大小
            },
            labelSize: "24%", // label字号
            rollFlag: true,
            threeLevel: true,
          }
        },
        //5.信访轨迹分析
        xfgjData:[]
      }
    },
    mounted() {
      this.getLeftAPI(this.year);
    },
    methods:{
      getLeftAPI(year) {
        // 1左侧查询
        get_http("xfrhxZdryhxLeftSeach", {year: year})
          .then(res => {
            this.getRightAPI(year);
          })
          .catch(err => {
            console.log(err);
          });
      },
      getRightAPI(year){
        // 2.基本信息
        get_http("xfrhxZdryhxJbxx", {year: year})
          .then(res => {
            this.jbxxData = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 3信访烈度
        get_http("xfrhxZdryhxXfldRadar", {year: year})
          .then(res => {
            this.xfldRadarParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 4投诉问题占比
        get_http("xfrhxZdryhxZbPie", {year: year})
          .then(res => {
            this.tswtPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // //5.信访轨迹分析
        get_http("xfrhxZdryhxXfgj", {year: year})
          .then(res => {
            this.xfgjData = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
      },
      leftNameClick(item){
        this.getRightAPI(this.year);
      },
      fontSize(res) {
        let docEl = document.documentElement,
          clientWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
        if (!clientWidth) return;
        let fontSize = 100 * (clientWidth / 1920);
        return res * fontSize;
      }
    }
  }
</script>

<style lang="stylus" scoped>
  .wrapper
    position relative
    top 0.8rem
    height calc(100% - 0.8rem)
    .content
      width 100%
      height 100%
      display flex
      padding .2rem .3rem
      box-sizing border-box
      .inner
        height calc(100% - 0.28rem)
      .w-left
        width 3rem
        height 100%
        position relative
        border-right 1px #91e3f7 dashed
      .w-right
        flex 1
        display flex
        .w-r-l
          flex 1
        .w-r-r
          flex 2
          display flex
          flex-direction column
          .top
            flex 1
            display flex
            .left
              flex 1
            .right
              flex 1
          .btm
            flex 1.3
</style>
