<template>
  <div class="btn4">
    <ul class="clearfloat">
      <li
              class="item-li"
              v-for="(item,index) of arr"
              :key="index"
              :class="curName===item?'active':''"
              @click="handleClick(item)"
      >
        <p>{{item}}</p>
        <ul class="erji-ul" v-show="item==='信访次数'&&curName===item&&erjiShow">
          <li
                  v-for="(it,i) of erjiArr"
                  :key="i"
                  :class="curErjiName===it?'erji-active':''"
                  @click.stop="handleErjiClick(it)"
          >{{it}}
          </li>
        </ul>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: "btn2",
    data() {
      return {
        arr: ["信访次数", "扬言", "专项"],
        curName: "",
        erjiArr: ["全部", "来信", "来访", "网信"],
        curErjiName: "",
        erjiShow: false
      }
    },
    methods: {
      handleClick(name) {
        if (this.curName === name) {
          if (this.curName === '信访次数') {
            this.erjiShow = !this.erjiShow;
            if (!this.curErjiName) {
              this.curName = (this.curName ? '' : this.curName);
            }
          } else {
            this.curName = '';
            this.curErjiName = '';
          }
        } else {
          this.curName = name;
          if (this.curName === '信访次数') {
            this.erjiShow = true;
          } else {
            this.erjiShow = false;
            this.curErjiName = '';
          }
        }
        if (this.curName !== '信访次数') {
          this.$emit("change", this.curName);
        }
      },
      handleErjiClick(it) {
        this.erjiShow = false;
        this.curErjiName = it;
        this.$emit("change", this.curErjiName);
      }
    }
  }
</script>

<style lang="stylus" scoped>
  .btn4
    position relative
    z-index 1
    width 250px
    height 55px
    background url("./img/btn4-bg.png") no-repeat
    background-size 100% 100%

    ul
      position relative
      top 20px

      li.item-li
        position relative
        float left
        padding 0 15px
        height 14px
        border-right 1px solid #5e6673
        cursor pointer

        &:last-child
          border-right none

        p
          line-height 30px
          margin -8px auto 0
          padding 0 7px
          color #8df6ff
          font-size 14px
          text-align center

        &.active
          p
            color #fff
            font-weight 700
            text-shadow 0 0 10px #276bf4
            border-bottom 2px solid #f3ff3a

        .erji-ul
          position absolute
          top 24px
          left 6px
          width 93px
          height 100px
          border 1px solid #06335d
          background-color #061229

          li
            color #8df6ff
            line-height 25px
            font-size 14px
            text-align center

            &.erji-active
              color #fff
              font-weight 700
              text-shadow 0 0 10px #276bf4
</style>
