<!--
  echarts 雷达图
  @auth:lcw
-->
<template>
  <div
    v-loading="!params.data.length"
    :element-loading-background="$STANDARD.loadingBgColor"
    ref="chartEl"
    :style="{width:params.canvasStyle.width,height:params.canvasStyle.height}"
  ></div>
</template>

<script>
  import * as echarts from 'echarts'

  export default {
    name: 'Radar',
    props: {
      params: {
        type: Object,
        default() {
          return {
            canvasStyle: {
              width: '410px',
              height: '360px'
            },
            labelMaxShow: true,//文本标签是否显示最大值
            data: [
              {
                "name": "硕士",
                "value": 0.45
              },
              {
                "name": "本科",
                "value": 0.45
              },
              {
                "name": "专科",
                "value": 0.45
              },
              {
                "name": "中专",
                "value": 0.45
              },
              {
                "name": "其他",
                "value": 0.45
              }
            ]
          }
        }
      },
      animateFlag: Boolean
    },
    data() {
      return {
        myChart: null
      }
    },
    mounted() {
      this.init();
    },
    methods: {
      init() {
        if (this.params.data.length === 0) return;

        if (this.myChart) {
          this.myChart.dispose();
          this.myChart = null;
        }

        this.myChart = echarts.init(this.$refs.chartEl);

        let propData = [...this.params.data];
        let options = this.params.options;
        //   fixData = [],
        //   indicator = [];
        //
        // data.forEach(item => {
        //   fixData.push(item.value);
        //   indicator.push({
        //     name: item.name,
        //     max: item.total
        //   })
        // });
        let indicator = [], data = [];
        for (let item of propData.values()) {
          let obj = {
            text: item.name,
            value: item.value,
            max: 1
          };
          indicator.push(obj);
          data.push(item.value);
        }
        let dataArr = [
          {
            value: data,
            label: {
              normal: {
                show: false,
                fontSize: 12,
                color: "#fff",
                formatter: (params) => {
                  return (params.value * 100).toFixed(0) + '%'
                }
              },
            },
            itemStyle: {
              normal: {
                fontSize: 12,
                color: "#fff",
                lineStyle: {
                  color: '#4A99FF',
                },
                shadowColor: '#4A99FF',
                shadowBlur: 10,
              },
            },
            areaStyle: {
              normal: { // 单项区域填充样式
                color: {
                  type: 'linear',
                  x: 0, //右
                  y: 0, //下
                  x2: 1, //左
                  y2: 1, //上
                  colorStops: [{
                    offset: 0,
                    color: 'rgba(196,113,245,0.4)'
                  }, {
                    offset: 1,
                    color: 'rgba(250,113,205,0.4)'
                  }],
                  globalCoord: false
                },
                opacity: 1 // 区域透明度
              }
            }
          }
        ];
        let option = {
          grid: {
            top: 0,
            left: 0,
            right: 0,
            bottom: 0
          },
          legend: {
            show: false,
          },
          radar: {
            center: options.center ? options.center : ['50%', '50%'],
            radius: options.radius ? options.radius : '75%',
            // shape: 'circle',
            name: {
              textStyle: {
                color: '#fff',
                fontSize: 12,
                lineHeight: 16,
                // align:'center'
              },
              formatter: (value, indicator) => {
                return value + '\n' + (indicator.value * 100).toFixed(0) + '%';
              }
            },
            indicator: indicator,
            splitArea: { // 坐标轴在 grid 区域中的分隔区域，默认不显示。
              show: true,
              areaStyle: { // 分隔区域的样式设置。
                color: ['rgba(255,255,255,0)', 'rgba(255,255,255,0)'], // 分隔区域颜色。分隔区域会按数组中颜色的顺序依次循环设置颜色。默认是一个深浅的间隔色。
              }
            },
            axisLine: { //指向外圈文本的分隔线样式
              lineStyle: {
                color: '#5762b8'
              }
            },
            // axisLabel: {
            //   color: "#000",
            //   fontSize: 12,
            // },
            splitLine: {
              lineStyle: {
                color: '#5762b8', // 分隔线颜色
                width: 1, // 分隔线线宽
              }
            },
          },
          series: [
            {
              type: 'radar',
              symbolSize: 8,
              // symbol: 'angle',
              data: dataArr
            }
          ]
        };
        this.myChart.setOption(option);
      }
    },
    watch: {
      'params.data': {
        handler(newVal, oldVal) {
          this.init();
        },
        deep: true
      }
    }
  }
</script>

<style lang="stylus" scoped>

</style>
